.footer {
    position: fixed;
    bottom: 0px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    background-color: rgba(0,0,0,0.5) !important;
    height: 50px;
    font-size: 12px;
    vertical-align: middle;
    line-height: 50px;
    color: #ffffff;
}

a {
    color: #000 !important;
}

a:hover {
    color: rgba(255,255,255,0.2) !important;
    text-decoration: none !important;
}